html {
	font-size: 10px;
	font-family: roboto;
}

* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	background-color: rgb(244, 244, 244);
}
p {
	font-size: 1.6rem;
}
small {
	font-size: 1.2rem;
}
.container {
	margin: 0 auto;
	max-width: 1000px;
	padding: 40px;
}
.title {
	font-size: 4.4rem;
	text-align: center;
}

.form {
	display: grid;
}

.label {
	font-weight: 800;
	font-size: 1.2rem;
	margin-bottom: 0.2rem;
	text-transform: uppercase;
}

.input {
	font-size: 1.6rem;
	padding: 0.5rem 2rem;
	line-height: 2.8rem;
	border-radius: 20px;
	border: 1px solid #ddd;
	margin-bottom: 1rem;
}

.button {
	background-color: rgba(0, 0, 0, 0.75);
	color: #fff;
	padding: 1rem 2rem;
	border: 1px solid rgba(0, 0, 0, 0.75);
	border-radius: 20px;
	font-size: 1.4rem;
	cursor: pointer;
	transition: background-color 250ms;
}

.button:hover {
	background-color: rgba(0, 0, 0, 0.85);
}

@media (min-width: 786px) {
	.form {
		grid-template-columns: auto 1fr auto;
		grid-gap: 1rem;
		align-items: center;
	}
	.input {
		margin-bottom: 0;
	}
}

.cardList {
	margin: 1rem 0;
	background-color: #fff;
	border: 1px solid #777;
	box-shadow: 1px 1px 8px #999;
	padding: 1rem;
	display: grid;
	grid-template-columns: auto 1fr;
	grid-template-rows: 40px 20px 254px;
	grid-gap: 1rem;
	grid-template-areas: "i t" "i v" "i o";
}
.cardList > .frame {
	width: 220px;
	height: 330px;
	grid-area: i;
}
.cardList > h2 {
	grid-area: t;
	font-size: 2rem;
}
.cardList > h3 {
	grid-area: v;
}
.cardList > p {
	grid-area: o;
	overflow-y: auto;
}
